* {
	padding: 0px;
	margin: 0px;
	border: 0px;
 }
 *,
 *:before,
 *:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
 }
 :focus,
 :active {
	outline: none;
 }
 a:focus,
 a:active {
	outline: none;
 }
 aside,
 nav,
 footer,
 header,
 section {
	display: block;
 }
 html,
 body {
	height: 100%;
	min-width: $minWidth;
 }
 body {
	line-height: 1.5;
	font-family: $fontFamily;
	//text-rendering: optimizeLegibility;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
 }
 input,
 button,
 textarea {
	font-family: $fontFamily;
 }
 textarea {
	resize: none; /*remove the resize handle on the bottom right*/
	overflow: auto;
	vertical-align: top;
	box-shadow: none;
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
 }
 input::-ms-clear {
	// display: none;
 }
 button {
	cursor: pointer;
 }
 button::-moz-focus-inner {
	padding: 0;
	border: 0;
 }
 a,
 a:hover,
 a:visited,
 a:active {
	text-decoration: none;
 }
 
 a:hover {
	cursor: pointer;
 }
 ul {
	padding: 0;
	margin: 0;
 }
 ul li {
	list-style: none;
 }
 img {
	vertical-align: top;
 }
