/* Banner */
.banner {
	position: relative;
	overflow: hidden;
 }
 .img-fluid {
	max-width: 100%;
	height: auto;
}
 
 .banner-header {
	position: absolute;
	z-index: 5;
	right: 0;
	top: 42px;
	height: 98px;
	width: 487px;
	font-weight: bold;
	font-size: 48px;
	line-height: 98px;
	text-align: center;
	color: #FFFFFF;
	background: linear-gradient(270deg, rgba(161, 179, 191, 0.8) 72.59%, rgba(161, 179, 191, 0) 98.05%);
 }

 

