/* Footer */
.footer {
	padding: 0 25px;
	box-sizing: border-box;
 }
 
 .footer-pay {
	display: flex;
	justify-content: space-between;
	margin: 12px auto 0;
 }
 .footer-pay-systems {
	width: 46px;
	height: 28px;
	border: 2px solid #FFFFFF;
	border-radius: 8px;
	background-color: #FFFFFF;
	background-repeat: no-repeat;
	background-image: url(../../img/footer-sprite.png);
 }
 .footer-pay-systems:not(:first-child) {
	margin-left: 5px;
 }
 .footer-pay-systems-paypal {
	background-position: -2px -6px;
 }
 .footer-pay-systems-master-card {
	background-position: -54px -8px;
 }
 .footer-pay-systems-visa {
	background-position: -104px -8px;
 }
 .footer-pay-systems-yandex {
	background-position: -154px -8px;
 }
 .footer-pay-systems-webmoney {
	background-position: -2px -46px;
 }
 .footer-pay-systems-qiwi {
	background-position: -52px -46px;
 }
 
 .footer-social-links {
	margin-top: 10px;
 }
 .footer-social-link {
	display: inline-block;
	width: 36px;
	height: 36px;
	margin: 0 5px;
	border-radius: 50%;
	background-repeat: no-repeat;
	background-image: url(../../img/footer-sprite.png);
 }
 .footer-social-link-twitter {
	background-position: -107px -42px;
 }
 .footer-social-link-vk {
	background-position: -157px -42px;
 }
 .footer-social-link:hover {
	cursor: pointer;
 }
 
 .footer-copyright {
	font-size: 0.8em;
 }
 
 .footer-contacts-phone {
	display: block;
 }
 
 .footer-contacts-email {
	display: block;
 }

 .footer-row {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	justify-items: center;
	gap: 20px;
	padding: 0px 20px;
 }
 .text-right {
	text-align: right;
	justify-self: end;
 }

 .footer-title {
	font-size: 20px;
 }
 .text-left {
	text-align: center;
	justify-self: start;
 }
 