.header-controls-search-form {
	position: absolute;
	right: 55px;
	top: calc(50% - 19px);
	display: block; 
	width: 233px;
	height: 38px;
	padding: 0 20px;
	background: #FFFFFF;
	box-shadow: 0px 0px 10px #D4E4E6;
	border-radius: 50px;
	text-align: right;
 }
 .header-controls-search-form .form-control {
	width: 100%;
	height: 100%;
	color: #333333;
	border: none;
	outline: none;
	box-shadow: none;
 }

 .invisible {
	opacity: 1;
 }

 